import React, { useState, useEffect, useMemo } from 'react';
import './App.css';
import { Select, Button } from 'antd';
import 'antd/dist/reset.css';
import html2canvas from 'html2canvas';
// import areaData from 'china-area-data';
// require the CJS data to avoid default import interop issues
const areaData = require('china-area-data');
// import heartImg from './assets/heart.gif';

// define area data type
type AreaItem = [string, string];

// format date to Chinese format
function formatDate(date: Date): string {
  const y = date.getFullYear();
  const M = date.getMonth() + 1;
  const d = date.getDate();
  const H = date.getHours();
  const m = date.getMinutes();
  const s = date.getSeconds();
  return `${y}年${M}月${d}日${H}时${m}分${s}秒`;
}

function App() {
  // province-city-district cascade data
  const provinces: [string, string][] = useMemo(() => {
    const obj = areaData['86'] || {};
    return Object.entries(obj);
  }, []);
  // states for selected codes
  const [province, setProvince] = useState<string>('');
  const [city, setCity] = useState<string>('');
  const [district, setDistrict] = useState<string>('');
  
  // initialize province once data is ready
  useEffect(() => {
    if (provinces.length) {
      setProvince(provinces[0][0]);
    }
  }, [provinces]);
  // update cities list based on province
  const cities: [string, string][] = useMemo(() => {
    if (!province) return [];
    const obj = areaData[province] || {};
    return Object.entries(obj);
  }, [province]);
  useEffect(() => {
    if (cities.length) {
      setCity(cities[0][0]);
    }
  }, [cities]);
  // update districts list based on city
  const districts: [string, string][] = useMemo(() => {
    if (!city) return [];
    const obj = areaData[city] || {};
    return Object.entries(obj);
  }, [city]);
  useEffect(() => {
    if (districts.length) {
      setDistrict(districts[0][0]);
    }
  }, [districts]);

  // real-time match time
  const [time, setTime] = useState(formatDate(new Date()));
  useEffect(() => {
    const timer = setInterval(() => setTime(formatDate(new Date())), 1000);
    return () => clearInterval(timer);
  }, []);

  console.log('provinces', provinces);
  console.log('province', province);
  console.log('cities', cities);
  console.log('city', city);
  console.log('districts', districts);
  console.log('district', district);

  if (!province || !city || !district || provinces.length === 0 || cities.length === 0 || districts.length === 0) {
    return (
      <div className="App">
        <header className="App-header">
          <div className="container">
            <p style={{ color: '#fff' }}>省市区数据加载中，请稍候…</p>
          </div>
        </header>
      </div>
    );
  }

  return (
    <div className="App">
      <header className="App-header">
        <div className="container" id="capture-area">
          {provinces.length === 0 ? (
            <p style={{ color: '#fff' }}>省市区数据加载中，请稍候…</p>
          ) : (
            <>
              <h1>全国同城约炮资源-匹配系统</h1>
              <div className="select-group">
                <Select
                  value={province}
                  style={{ width: 120 }}
                  onChange={val => {
                    setProvince(val);
                    const newCities = areaData[val] || {};
                    setCity(Object.keys(newCities)[0] || '');
                  }}
                  options={provinces.map(([code, name]) => ({ value: code, label: name }))}
                />
                <Select
                  value={city}
                  style={{ width: 120 }}
                  onChange={val => {
                    setCity(val);
                    const newDistricts = areaData[val] || {};
                    setDistrict(Object.keys(newDistricts)[0] || '');
                  }}
                  options={cities.map(([code, name]) => ({ value: code, label: name }))}
                />
                <Select
                  value={district}
                  style={{ width: 120 }}
                  onChange={val => setDistrict(val)}
                  options={districts.map(([code, name]) => ({ value: code, label: name }))}
                />
              </div>
              <div className="heart-container">
                <img className="heart" src={require('./assets/heart.gif')} alt="heart animation" />
              </div>
              <div className="search-tip">搜索中 请耐心等待…</div>
              <div className="desc">
                本平台承诺：所有优惠内容真实有效，保护双方信息
              </div>
              <div className="time">当前匹配时间：{time}</div>
            </>
          )}
        </div>
        <Button
          type="primary"
          style={{ marginTop: 24 }}
          onClick={async () => {
            const area = document.getElementById('capture-area');
            if (area) {
              const canvas = await html2canvas(area, { backgroundColor: null });
              const url = canvas.toDataURL('image/png');
              const a = document.createElement('a');
              a.href = url;
              a.download = 'screenshot.png';
              a.click();
            }
          }}
        >截图并下载</Button>
      </header>
    </div>
  );
}

export default App; 